<template>
    <div>
        <v-header>
        </v-header>
        <v-content>
            <div class="section">
                <div class="matter" v-link="{name:'real-step1'}">
                    <span class="imgs identify"></span>
                    <div class="title">身份认证</div>
                    <span class="img transfer"></span>
                </div>
                <div class="matter" v-link="{name:'certificate'}">
                    <span class="imgs skill"></span>
                    <div class="title">技能认证</div>
                    <span class="img transfer"></span>
                </div>
            </div>
        </v-content>
    </div>
</template>
<script>
    import {url, lt} from '../../const'
    import {toast} from '../../assets/js/toast'
    import VHeader from '../../components/Header'
    import VContent from '../../components/Content'

    export default{
        route:{
            data({from,to}){
                console.log('isReady:' + this.isReady);
                if(this.isReady){
                    return {
                        state:!this.state
                    }
                }
            }
        },
        ready(){
            this.$on('ready',this.init);
            this.$emit('ready');
            this.isReady = true;
        },
        data(){
            return {
                isReady:false,
                state:false,

                username:'',
                haspwd:true
            }
        },
        methods: {
            init(){
                if(lt.has(lt.keys.user)){
                    this.username = lt.getJson(lt.keys.user).userName;
                }
                this.$http.get(url.haspwdcheck).then(
                        function ({data:{success, data, resultMsg, code}}) {
                            if (success) {
                                this.haspwd = data.haspwd;
                            } else {
                                toast.alert(resultMsg);
                            }
                        },function (response){
                            toast.alert('网络异常');
                        })
            }
        },
        watch:{
            'state':function(){
                this.haspwd = true;
                this.username = '';
                this.$emit('ready');
            }
        },
        components: {VHeader, VContent}
    }
</script>

<style scoped>
    .matter .title {
        text-indent: .35rem;
        width: auto;
    }

    .imgs {
        width: .25rem;
        height: .25rem;
        background-size: .25rem;
        position: absolute;
        margin-top: .1rem;
    }

    .imgs.identify {
        background-image: url(../../assets/img/icon/a_identity.png);
    }

    .imgs.skill {
        background-image: url(../../assets/img/icon/a_skill.png);
    }
</style>


